import axios from "axios";
import { Button, Drawer , Table, message, Popconfirm, Typography  } from 'antd';
import React,{useState ,useEffect} from "react";
import {request} from "../../utils/request";
const { Paragraph } = Typography;
export const Head=() => {
    const [visible,setVisible]=useState(false);
    const [data,setData]=useState([]);
    
    useEffect(()=>{
        if(visible){
            request.post('/auth/login',{
              "email": "test@gmail.com",
              "password": "123456"
            }).then(()=>{
            request.post('/feature/get-image-feature-config',{
                imageVersion:'test1',
            })
            .then((res) =>{
                console.log(res);
                setData(res.data.data);
            });})
        }
    },[visible])
    const showDrawer = () => {
        setVisible(true);
      };
    const onClose = () => {
        setVisible(false);
    };
      
    const columns = [
        {
          title: '',
          dataIndex: 'featureName',
          width:300,
          render: (text,row,index)=>{
            return <Paragraph
              editable={{
               autoSize: false,
               onChange: (newVal)=>{
                  if(newVal == "") return
                  request.post('/feature/update-image-feature-config',{
                    dataFormat: "",
                    defaultValue: "false",
                    featureName: newVal,
                    id: row.id,
                    remark: row.remark,
                    type: "checkbox",
                    valid: true,
                    value: null,
                 })
                 .then(()=>{
                  request.post('/feature/get-image-feature-config',{
                    imageVersion:'test1',
                  })
                   .then((res) =>{
                      console.log(res);
                      setData(res.data.data);
                  });})
                }
              }}
            >
            {text}
            </Paragraph>
          }
        },
        {
          title: '',
          dataIndex: 'remark',
          width:400,
          render: (text,row,index)=>{
            return <Paragraph
              editable={{
               autoSize: false,
               onChange: (newVal)=>{
                  if(newVal == "") return
                  request.post('/feature/update-image-feature-config',{
                    dataFormat: "",
                    defaultValue: "false",
                    featureName: row.featureName,
                    id: row.id,
                    remark: newVal,
                    type: "checkbox",
                    valid: true,
                    value: null,
                 })
                 .then(()=>{
                  request.post('/feature/get-image-feature-config',{
                    imageVersion:'test1',
                  })
                   .then((res) =>{
                      console.log(res);
                      setData(res.data.data);
                  });})
                }
              }}
            >
            {text}
            </Paragraph>
          }
        },
        {
          title:<Button type="link" onClick={()=>{
//            const da1=[{id:"-1",featureName:"",remark:""}]
//            data.forEach(item=>{
//              da1.push(item)
//            })
//            setData(da1)
              request.post('/feature/add-image-feature-config',{
                dataFormat: "",
                defaultValue: "false",
                featureName: "("+Math.ceil(Math.random()*100000)+")",
                imageVersion: "test1",
                remark: "",
                type: "checkbox"
              })
              .then(()=>{
                 request.post('/feature/get-image-feature-config',{
                 imageVersion:'test1',
              })
              .then((res) =>{
                console.log(res);
                setData(res.data.data);
              });})
          }}></Button>,
          dataIndex:'operate',
          width:100,
          render: (text,row,index)=>{
            return   <Popconfirm
            title={<div> <span style={{color:'red'}}>{row.featureName}</span> </div>}
            onConfirm={()=>{
              request.post('/feature/delete-image-feature-config',{
                id: row.id
              }).then(()=>{
              request.post('/feature/get-image-feature-config',{
                imageVersion:'test1',
              })
              .then((res) =>{
                console.log(res);
                setData(res.data.data);
              });})
            }}
            okText=""
            cancelText=""
            >
            <a href="#">&nbsp;&nbsp;&nbsp;&nbsp;</a>
            </Popconfirm>
          }
        }
    ];

    return<>
    <Button type="primary" onClick={showDrawer}></Button>
    <Drawer title=" Image Feature Setting" placement="left" onClose={onClose} visible={visible} width={800}>
        <Table dataSource={data} columns={columns} pagination={false} />
    </Drawer>
    </>
};

export default Head;